import { defineComponent, computed, ref } from 'vue';
import { useStore } from '@/store';
import { useRouter, useRoute } from 'vue-router';
import { PathMapToMenu } from '@/utils/map-menus';
export default defineComponent({
  name: 'NavMenu',
  props: {
    isCollapse: {
      type: Boolean,

      default() {
        return false;
      }

    }
  },

  setup() {
    //store操作
    const store = useStore();
    const userMenus = computed(() => store.state.login.userMenus); //router操作

    const router = useRouter(); //刷新页面Menu操作

    let currentPath = useRoute().path;
    let menu = PathMapToMenu(userMenus.value, currentPath); //data

    const defaultId = ref(menu.id + '');

    const handleMenuItemClick = item => {
      router.push({
        path: item.url ?? '/not-found'
      });
    };

    return {
      userMenus,
      handleMenuItemClick,
      defaultId
    };
  }

});